body {
  color: #1a4f63;
  background-color: #f3efe7;
  flex-direction: column;
  gap: 20px;
  margin: 0;
  padding: 20px;
  font-family: Poppins, sans-serif;
  display: flex;
  overflow-x: hidden;
}

body a {
  color: #8c7a6b;
  transition: all .3s ease-in-out;
}

body a:hover {
  color: adjust(#1a4f63, 10%);
}

body h1, body h2, body h3, body h4, body h5, body h6 {
  color: #8c7a6b;
  font-family: Libre Baskerville, serif;
}

body p {
  color: #2b2b2b;
  text-align: center;
  font-family: Poppins, sans-serif;
  line-height: 1.5;
}

footer {
  text-align: center;
  border-top: 2px solid #8c7a6b;
  width: 100%;
  padding: 10px 0;
  position: relative;
}

footer div {
  flex-direction: column;
  gap: 4px;
  display: flex;
}

footer p {
  color: #2b2b2b;
  margin: 0;
  font-size: 14px;
}

.introduccio h2 {
  color: #1a4f63;
  text-align: center;
  font-size: 32px;
  font-style: italic;
  font-weight: normal;
}

.cards-container {
  flex-wrap: wrap;
  gap: 20px;
  width: 100%;
  max-width: 100%;
  display: flex;
}

.cards-container .card {
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 20px;
  flex: calc(100% - 20px);
  max-width: 100%;
  padding: 20px;
  box-shadow: 0 4px 6px #00000040;
}

.cards-container .card img {
  border-radius: 12px;
  width: 100%;
  height: auto;
}

.cards-container .card h3 {
  color: #2b2b2b;
  font-family: Poppins, sans-serif;
  font-size: 22px;
  font-weight: normal;
}

.cards-container .card .btn-more-info {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #1a4f63;
  border: none;
  border-radius: 8px;
  width: 100%;
  padding: 10px 16px;
  font-size: 16px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: block;
}

.cards-container .card .btn-more-info:hover {
  background-color: #8c7a6b;
}

.cards-container .card .btn-more-info.blocked {
  cursor: not-allowed;
  background-color: gray;
}

header {
  box-sizing: border-box;
  border-bottom: 2px solid #8c7a6b;
  justify-content: space-between;
  align-items: flex-end;
  width: 100%;
  padding: 10px 0;
  display: flex;
  position: relative;
}

header nav {
  box-sizing: border-box;
  padding: 0;
  padding-top: calc(var(--header-height)  + 2px);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  z-index: 1000;
  background: none;
  justify-content: center;
  align-items: flex-start;
  transition: background .3s, opacity .3s, visibility .3s;
  display: flex;
  position: fixed;
  inset: 0;
  overflow-y: auto;
}

header nav > .menu-box {
  box-sizing: border-box;
  background: none;
  flex-direction: column;
  align-items: center;
  gap: 18px;
  width: 100%;
  max-width: 480px;
  padding: 12px 20px;
  display: flex;
}

header nav ul {
  flex-direction: column;
  align-items: center;
  gap: 18px;
  width: 100%;
  margin: 0;
  padding: 6rem 0 0;
  list-style: none;
  display: flex;
}

header nav a {
  box-sizing: border-box;
  color: #f3efe7;
  text-align: center;
  width: 100%;
  padding: 12px 16px;
  font-size: 1.25rem;
  text-decoration: none;
  display: block;
}

header .icons {
  z-index: 1100;
  order: 0;
  width: 44px;
  height: 44px;
  margin-right: 12px;
  display: block;
  position: relative;
}

header .icons button {
  width: 100%;
  height: 100%;
  font: inherit;
  color: inherit;
  cursor: pointer;
  appearance: none;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  line-height: 0;
  display: inline-flex;
  position: absolute;
  inset: 0;
}

header .icons .icon-default {
  display: block;
}

header .icons .icon-active {
  color: #f3efe7;
  display: none;
}

header h1 {
  margin: 0;
  font-size: 24px;
  font-style: italic;
}

header h1 img {
  max-width: 125px;
}

header.is-active .icon-default {
  display: none;
}

header.is-active .icon-active {
  display: block;
}

header.is-active nav {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  background: #000000b3;
}

.introduccio img {
  border-radius: 12px;
  width: 100%;
  height: auto;
}

.list-icones {
  flex-direction: column;
  align-items: center;
  gap: 1em;
  display: flex;
}

.list-icones span {
  color: #2b2b2b;
}

.mes-informacio, .mes-informacio .imatges {
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.mes-informacio .imatges img {
  border-radius: 12px;
  width: 100%;
}

@media (width >= 769px) {
  body {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 80px;
  }

  header {
    padding: 18px 0;
  }

  header .icons {
    display: none;
  }

  header h1 {
    color: #8c7a6b;
    flex: none;
    order: 0;
    font-size: 28px;
  }

  header h1 img {
    max-width: 200px;
  }

  header nav {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    background: none;
    order: 1;
    margin-left: auto;
    padding: 0;
    position: static;
  }

  header nav > .menu-box {
    background: none;
    max-width: none;
    padding: 0;
  }

  header nav ul {
    flex-direction: row;
    align-items: baseline;
    gap: 32px;
    padding: 2rem 0 0;
  }

  header nav a {
    color: #1a4f63;
    border-bottom: 2px solid #0000;
    width: auto;
    padding: 6px 0;
    font-size: 16px;
    transition: border-color .16s, color .16s;
  }

  header nav a:hover, header nav a:focus {
    color: #1a4f63;
    border-bottom-color: #1a4f63;
  }

  header nav a.active {
    color: #1a4f63;
    border-bottom-color: #1a4f63;
    font-weight: 700;
  }

  .introduccio h2 {
    text-align: center;
    font-size: 36px;
  }

  .introduccio p {
    text-align: center;
    font-size: 16px;
  }

  .cards-container {
    flex-flow: row;
    gap: 20px;
    width: 100%;
    max-width: 100%;
    display: flex;
  }

  .cards-container .card {
    flex: calc(33.333% - 20px);
    max-width: calc(33.333% - 20px);
  }

  .card-grid {
    flex-wrap: wrap;
    justify-content: center;
  }

  footer {
    margin: 4em 0 .1em;
  }

  footer div {
    flex-direction: row;
    justify-content: space-between;
  }

  .imatge-detall {
    flex-direction: row;
    display: flex;
  }

  .imatge-detall img {
    flex: 50%;
  }

  .imatge-detall .descripcio {
    flex-direction: column;
    flex: 50%;
    gap: .5em;
    padding: 0 20px;
    display: flex;
  }

  .imatge-detall .descripcio h2 {
    text-align: left;
  }

  .imatge-detall .descripcio p {
    text-align: left;
    margin: 0;
    line-height: 1.5;
  }

  .imatge-detall .list-icones {
    align-items: flex-start;
    padding-top: 1em;
  }

  .mes-informacio .imatges {
    flex-direction: row;
    gap: 1em;
  }

  .mes-informacio img {
    border-radius: 12px;
    width: 100%;
  }

  .mes-informacio h3 {
    margin-top: 2em;
    margin-bottom: 0;
  }

  .mes-informacio p {
    text-align: left;
  }
}
